
import { API } from "@/core";
import { OpenWeather, WeatherMoment } from "@/interfaces/weather.interface";
import { defineComponent, onMounted } from "@vue/runtime-core";
import { reactive } from "vue";
export default defineComponent({
  name: "CateWeather",
  setup() {
    const dailyMoment = reactive({
      background: "Cloud",
    });
    const weather = reactive<{
      error: string;
      query: string;
      weather: OpenWeather;
    }>({
      error: "",
      query: "",
      weather: {
        coord: {
          lon: 105.8412,
          lat: 21.0245,
        },
        weather: [
          {
            id: 802,
            main: "Clouds",
            description: "scattered clouds",
            icon: "03d",
          },
        ],
        base: "stations",
        main: {
          temp: 14,
          feels_like: 12.84,
          temp_min: 14,
          temp_max: 14,
          pressure: 1025,
          humidity: 53,
        },
        visibility: 10000,
        wind: {
          speed: 1.42,
          deg: 6,
        },
        clouds: {
          all: 35,
        },
        dt: 1640746578,
        sys: {
          type: 1,
          id: 9308,
          country: "VN",
          sunrise: 1640734350,
          sunset: 1640773462,
        },
        timezone: 25200,
        id: 1581130,
        name: "Hanoi",
        cod: 200,
      },
    });
    onMounted(() => {
      dailyMoment.background = getCurrentMoment();
    });

    const getCurrentMoment = () => {
      const hour = new Date().getHours();
      if ((hour < 5 && hour >= 0) || (hour > 20 && hour <= 24)) {
        return WeatherMoment.MID_NIGHT;
      }
      if (hour < 10 && hour >= 5) {
        return WeatherMoment.MORNING;
      }
      if (hour < 13 && hour >= 10) {
        return WeatherMoment.NOON;
      }
      if (hour < 18 && hour >= 13) {
        return WeatherMoment.AFTERNOON;
      }
      return WeatherMoment.EVENING;
    };
    return { weather, dailyMoment };
  },
  methods: {
    fetchWeather(e: any) {
      this.weather.error = "";
      if (e.key === "Enter") {
        fetch(
          `${API.WEATHER.URL_BASE}weather?q=${this.weather.query}&units=metric&APPID=${API.WEATHER.KEY}`
        )
          .then((res) => {
            return res.json();
          })
          .then(this.setResults);
      }
    },
    setResults(results: OpenWeather) {
      console.log(results);

      if (results.message) {
        this.weather.error = results.message;
      } else {
        this.weather.weather = results;
        this.weather.error = "";
        this.dailyMoment.background = this.weather.weather.weather[0].main;
      }
    },
    locatorButtonPressed() {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          console.log(position.coords.latitude);
          console.log(position.coords.longitude);
        },
        (error) => {
          console.log(error.message);
        }
      );
    },
  },
});
